.board {
    width: 350px;
    height: 420px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: repeat(6, 1fr);
    grid-gap: 5px;
}

.board-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    max-width: 500px;
    height: 100%;
}

.row {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 5px;
}

.tile {
    width: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    line-height: 2rem;
    font-weight: bold;
    vertical-align: middle;
    box-sizing: border-box;
    color: var(--tile-text-color);
    text-transform: uppercase;
    user-select: none;
}

.empty {
    border: 2px solid rgb(58, 58, 60)
}

.correct {
    background-color: rgb(83, 141, 78);
}

.present {
    background-color: rgb(181, 159, 59);
}

.absent {
    background-color: rgb(58, 58, 60);
}